<template>
    <div :class="classlist" :style="{ 'background-image': `url(${imageUrl})` }">
        <button class="tued-btn" type="button" @click="extension.send('chose-image')" v-tooltip :title="translator.trans('selectImage')"><i class="fa-solid fa-pen"></i></button>
        <button class="tued-btn" v-if="props.modelValue.id" type="button" @click="extension.send('remove-image')" v-tooltip :title="translator.trans('clearImage')" style="left: initial; right: 5px;"><i class="fa-solid fa-close"></i></button>
        <slot />
    </div>
</template>

<script setup>
const { defineProps, defineEmits, computed, inject, onMounted, onUnmounted } = require('vue');
const props = defineProps(['class', 'modelValue', 'instanceId']);
const options = inject('options');
const extensions = inject('extensions.registry');
const filemanager = extensions.render('Filemanager');
const translator = inject('translator');
const extension = inject('instance.extensions').editor('BackgroundImage');
const emit = defineEmits(['update:modelValue']);

const classlist = computed(() => {
    return 'tued-block_editable-background-image ' + props.class;
});

const imageUrl = computed(() => {
    if (props.modelValue.id) {
        return filemanager.generatePreviewImagePath(props.modelValue);
    }

    return 'data:image/png;base64,';
});

extension.receive('image-chosen', (data) => {
    emit('update:modelValue', {
        id: data.id,
        filename: data.filename,
    });
});

onUnmounted(() => extension.unmount());
</script>
<script>
export default {name: 'Extension.BackgroundImage.Editor'}
</script>
